input, textarea {
    outline: 0;
    border-width: 0 0 1px;
    border-color: var(--dark-gray);
    width: 49%;
    height: 40px;
    text-align: left;
  }

input:focus {
    border-width: 0 0 1px;
    border-color: #000000;
}

form button {
    margin-top: 2rem;
}

.btn {
    padding: 10px 35px;
    border-radius: 40px;
    border: none;
    background-color: var(--dark-gray);
    color: var(--off-white);
    font-size: 18px;
}

button:hover, .submit-btn:hover {
    cursor: pointer;
    opacity: 80%;
    transition: 500ms;
}

button:focus {
    outline: none;
}

.contact {
    display: flex;
    align-items: center;
    padding: 2rem 5rem 2rem 5rem;
}

.contact h3 {
    font-size: 24px;
    margin-bottom: 1rem;
}

.form {
    display: flex;
    flex: 60%;
    flex-direction: column;
    padding-left: 3rem;
    padding-right: 5%;
}

.name-email {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
}

.contact-image {
    flex: 40%;
    background-color: transparent;
    justify-content: center;
}

.contact-image img {
    background-color: transparent;
    width: auto;
    max-height: 500px;
}

.message, textarea {
  width: 100%;
  /* height: 200px; */
  align-content: flex-end;
}

.submit-btn {
    border: none;
    border-radius: 20px;
    text-align: center;
    width: 25%;
    margin-top: 15px;
    background-color: var(--dark-gray);
    color: var(--off-white);
    font-size: 18px;
}


@media only screen and (max-width: 900px) {
    .form {
        order: 2;
    }

    .contact {
        flex-direction: column;
    }

    .form {
        order: 2;
        width: 80%;
    }

    .contact-image {
        display: flex;
    }

    .contact-image img {
        max-width: 50%;
        height: auto;
    }

  }

  @media only screen and (max-width: 650px) {
    .form {
        width: 95%;
        padding-left: 0;
        padding-right: 0;
    }
  }

  @media only screen and (max-width: 635px) {
      .contact-image img {
          max-width: 50%;
          padding-bottom: 5%;
          display: block;
          margin-left: auto;
          margin-right: auto;
    }
  }
