        /* justify-content: center; */
    }

    .hero img {
        max-width: 50%;
        /* height: auto; */
        padding-bottom: 5%;
    }

    .hero-left {
        padding-right: 0;
        -webkit-order: 2;
                order: 2;
    }

    .hero-right {
        padding-left: 0;
        -webkit-justify-content: center;
                justify-content: center;
    }
  }

  @media only screen and (max-width: 635px) {
      .project-info h3 {
          padding-top: 5%;
      }

      .hero img, .contact-image img {
          max-width: 50%;
          padding-bottom: 5%;
